let inputs = document.querySelectorAll('.input')

inputs.forEach(function (input) {
    input.addEventListener('input', function () {
        if (input.value.length) {
            input.classList.add('filled');
        }
        else {
            input.classList.remove('filled');
        }
    })
});


//lazy laod

document.addEventListener("DOMContentLoaded", function () {
    yall({
        observeChanges: true,
        threshold: 400,
    });
});
$(function () {
    let show = 'show';

    $('input').on('checkval', function () {
        let label = $(this).next('label');
        if (this.value !== '') {
            label.addClass(show);
            this.style.opacity = 1;
        } else {
            label.removeClass(show);
            this.style.opacity = 0.6;
        }
    }).on('keyup', function () {
        $(this).trigger('checkval');
    });
});
let map, marker, infowindow, geocoder, addresBuilding, addresBuildingText;

let locLink = document.querySelectorAll('.mapTarget')

locLink.forEach((address) => {
    address.addEventListener('click', (e) => {
        addresBuildingText = e.target.textContent
        addresBuildingTextFromDataAdress = address.getAttribute('data-addres')
        geocode(addresBuildingText)
    })
})


function initMap() {
    addresBuilding = { lat: 40.648583, lng: -73.873768 };
    let latc = addresBuilding.lat - 0.02
    let lngc = addresBuilding.lng + 0.02
    let centerMap = { lat: latc, lng: lngc }

    map = new google.maps.Map(document.getElementById('map'), {
        center: centerMap,
        zoom: 13,
        mapTypeControl: false,
        streetViewControl: false,
        zoomControl: false,
        fullscreenControl: false,
        // gestureHandling: 'greedy',
    });
    $.getJSON("./js/mstyle.json", function (data) {
        map.setOptions({ styles: data });
    });

    marker = new google.maps.Marker({
        position: addresBuilding,
        map: map,
        icon: {
            url: 'img/label.png',
            scaledSize: new google.maps.Size(100, 100)
        },
        animation: google.maps.Animation.DROP
    });

    geocoder = new google.maps.Geocoder();
}

function geocode(address) {
    geocoder.geocode({ 'address': address }, function (results, status) {

        if (status == 'OK') {
            let latLng = results[0].geometry.location
            let mlat = latLng.lat() - 0.02;
            let mlng = latLng.lng() + 0.02;
            let marcenter = { lat: mlat, lng: mlng }
            map.setCenter(marcenter);
            marker.setPosition(latLng)
            marker.setAnimation(google.maps.Animation.BOUNCE);
            setTimeout(() => {
                marker.setAnimation(null)
            }, 2000)

            marker.setTitle(`${address}`)
        } else {
            geocode(addresBuildingTextFromDataAdress)
        }
    });
}
(function () {
    'use strict'
    let btn = document.getElementById('toggler')
    let close = document.getElementById('close')
    let links = document.querySelectorAll('.menu__link')

    btn.addEventListener('click', (e) => {
        e.preventDefault();
        btn.classList.add('menu--opened')
        btn.style.display = 'none'
    })

    close.addEventListener('click', (e) => {
        btn.classList.remove('menu--opened')
        btn.style.display = 'flex'
    })


    links.forEach((link) => {
        link.addEventListener('click', (e) => {
            if (document.documentElement.clientWidth <= 992) {
                btn.classList.remove('menu--opened')
                btn.style.display = 'flex'
            }
        })
    })

    let w = null;
    window.addEventListener('resize', () => {
        w = document.documentElement.clientWidth
        if (w >= 992) {
            btn.style.display = 'none'
        } else if (w <= 992 && !btn.classList.contains('menu--opened')) { btn.style.display = 'flex' }
    })


    let sections = document.querySelectorAll('.bg');

    window.addEventListener('scroll', () => {
        w = document.documentElement.clientWidth

        btn.classList.remove('menu--opened')
        if (w <= 992) {
            btn.style.display = 'flex'
        }
        let firstViewportSection = null;
        sections.forEach((section) => {
            let rect = section.getBoundingClientRect();

            if (!firstViewportSection) {
                if (rect.bottom > 60) {
                    firstViewportSection = section;
                }
            }

        })

        if ((!firstViewportSection.classList.contains('toggler--light'))) {
            btn.style.color = '#7e5aff';
        } else {
            btn.style.color = '#fff';
        }
    })



})()